.login--main {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  justify-items: flex-start;
  align-items: center;
  padding: 3rem 0.8rem;
  background-color: #fffcf6;
}
.login__svg__container {
  display: none;
}
.login__svg__container img {
  height: 28rem;
  display: block;
}
.login__form__container {
  width: 100%;
  margin: 0 auto;
  padding: 1.5rem 1rem;
  border-radius: 3px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
  background-color: var(--color-white);
}
.login__form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.3rem;
  font-family: var(--primary-font);
  color: var(--input-label-color);
}
.login__form legend {
  line-height: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
  font-family: var(--tertiary-font);
  color: var(--legend-color);
  margin-bottom: 0.75rem;
  text-decoration: 2px underline currentColor;
  text-underline-offset: 2px;
}

.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.form__group label {
  font-size: 0.95rem;
  font-weight: 600;
  line-height: 1.2rem;
}
.form__group input {
  font-family: var(--primary-font);
  font-size: 0.95rem;
  width: 100%;
  padding: 0.45rem 0.5rem;
  border: 1px solid var(--input-field-border);
  border-radius: 2px;
  outline: none;
  border-radius: 3px;
}
.form__group input:focus {
  outline: 1px solid var(--color-black);
}
.form__remember__group label {
  font-size: 0.95rem;
  font-weight: 600;
  line-height: 1.2rem;
  margin-left: 0.5rem;
}
.form__submit__group input {
  padding: 0.75rem 1.5rem;
}

/************************
password forgot link and register now link
*************************/
.forgot__password__link,
.register__now__link {
  text-decoration: none;
  color: var(--form-link-color);
  display: inline-block;
  font-size: 0.9rem;
  font-family: var(--tertiary-font);
}
.forgot__password__link:hover, .forgot__password__link:focus,
.register__now__link:hover,
.register__now__link:focus {
  text-decoration: 1px underline currentColor;
  text-underline-offset: 2px;
}

.forgot__password__link {
  margin-top: 0.25rem;
  font-weight: 500;
}

.registration__page__link__container {
  font-weight: 500;
  font-size: 0.9rem;
}

/************************
media queries
*************************/
@media screen and (min-width: 481px) {
  .login__form__container {
    width: 90%;
    padding: 1.5rem;
  }
}
@media screen and (min-width: 760px) {
  .login__form__container {
    width: 80%;
  }
  .login--main {
    padding: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .login--main {
    grid-template-columns: repeat(2, 1fr);
  }
  .login__form__container {
    width: 100%;
  }
  .login__svg__container {
    display: block;
  }
}